<template>
  <div id="courses">
    <el-row>
      <el-col>
        <el-card class="card-box pt-0" shadow="hover">
          <search-courses-form
            @searchOrders="searchCourses" @clearSearchConditions="clearSearchConditions"></search-courses-form>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-card class="card-box" shadow="hover">
          <el-row>
            <el-col><span class="count ml-5">数据统计：共{{total}}条</span></el-col>
          </el-row><br>
          <el-row>
            <el-col>
              <course-table :tableData="courses"  :isPagination="isPagination"
                :page="currentPage" :total="total"
                @changeCurrentPage="changeCurrentPage" @checkCourseDetail="checkCourseDetail"></course-table>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row><br>
  </div>
</template>
<script>
/**
* @author 吴永军
* @description 课程列表
*/
import SearchCoursesForm from './SearchForm'
import CourseTable from './CourseTable'
import { fakeCourses, transCourses } from './table.config.js'
export default {
  name: 'courses',
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 0,
      searchParams: {},
      courses: [],
      isPagination: true
    }
  },
  components: {
    'search-courses-form': SearchCoursesForm,
    'course-table': CourseTable
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['课程管理', '课程信息'])
    this.courses = transCourses(fakeCourses.items)
    this.total = fakeCourses.total
  },
  methods: {
    searchCourses (params) {
      this.searchParams = params
      this.fetchCourses()
    },
    clearSearchConditions (params) {
      this.searchParams = params
      this.fetchCourses()
    },
    fetchCourses () {
    },
    changeCurrentPage (currentPage) {
      this.currentPage = currentPage
      this.fetchCourses()
    },
    checkCourseDetail (course) {
      this.$router.push(`/courseDetail/${course.id}`)
    }
  }
}
</script>
<style scoped>
@import "../../../assets/styles/common.css";
.ml-5 {
  margin-left: 5px;
}
</style>



