<template>
  <div id="course-search-form">
    <el-form :inline="true" :model="courseForm">
      <el-form-item label="课程名称">
        <el-input v-model="courseForm.courseName" placeholder="课程名称"></el-input>
      </el-form-item>
      <el-form-item label="课程类型">
        <el-select v-model="courseForm.courseType" placeholder="课程类型">
          <el-option :label="item.title" :value="item.id" v-for="item in courseTypes" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="主讲人">
        <el-input v-model="courseForm.lecturer" placeholder="主讲人"></el-input>
      </el-form-item>
      <el-form-item label="地域">
        <el-select v-model="courseForm.region" placeholder="地域">
          <el-option :label="item.title" :value="item.id" v-for="item in regions" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search">查询</el-button>
        <el-button type="default" @click="clear">清除筛选项</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
/**
* @author 吴永军
* @description 课程列表查询表单
*/
export default {
  name: 'courseSearchForm',
  data () {
    return {
      courseForm: {
        courseName: '',
        courseType: -1,
        lecturer: '',
        region: -1
      },
      courseTypes: [],
      regions: []
    }
  },
  created () {
    this.fetchCourseTypes()
    this.fetchRegions()
  },
  methods: {
    fetchCourseTypes () {
      this.courseTypes = [{ id: -1, title: '全部' }, { id: 1, title: '类型一' }, { id: 2, title: '类型二' }]
    },
    fetchRegions () {
      this.regions = [{ id: -1, title: '全部' }, { id: 1, title: '北京' }, { id: 2, title: '昆明' }]
    },
    search () {
      this.$emit('searchCourses', this.courseForm)
    },
    clear () {
      this.courseForm = {
        courseName: '',
        courseType: -1,
        lecturer: '',
        region: -1
      }
      this.$emit('clearSearchConditions', this.services)
    }
  }
}
</script>



