<template>
  <div class="course-container" id="course-table">
    <div>
      <el-row>
        <el-col class="p-5" :span="6" v-for="(item, index) in tableData" :key="index">
          <el-card :body-style="{ padding: '0px', 'line-height': '25px' }" shadow="hover">
            <el-image class="poster" :src="item.poster" fit="cover"></el-image>
            <div class="content">
              <el-row>
                <el-col :span="16"><span>{{ item.title }}</span></el-col>
                <el-col class="ta-right" :span="8">
                  <span class="studying-number">
                    <i class="fa fa-user"></i> {{ item.studyingNumber }}人学习
                  </span>
                </el-col>
              </el-row>
              <el-row>
                <el-col><span class="label">【{{ item.label }}】</span></el-col>
              </el-row>
              <el-row>
                <el-col>
                  <span class="lecturer">主讲人：{{ item.lecturer }}</span>
                  <span class="p-title">【{{ item.professionalTitle }}】</span>
                </el-col>
              </el-row>
              <el-row>
                <el-col>
                  <span class="exporter">*{{ item.productor }}</span>
                </el-col>
              </el-row>
              <el-row>
                <el-col>
                  <span class="old-price">价格：￥{{ item.oldPrice }}</span>&nbsp;
                  <span class="sale-price">￥{{ item.salePrice }}</span>
                </el-col>
              </el-row>
              <el-row>
                <el-col>
                  <span class="region-price">您名下的阿姨享受优惠价格</span>
                </el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div><br>
    <div class="pagination" v-if="isPagination">
      <el-pagination :current-page="currentPage" :page-sizes="[10, 20, 30]" :page-size="pageSize" :total="total"
        layout="prev, pager, next, jumper" background
        @size-change="handleSizeChange" @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>
<script>
/**
* @author 吴永军
* @description 课程管理表格组件
*  */
export default {
  name: 'courseTable',
  props: ['tableData', 'isPagination', 'page', 'total'],
  data () {
    return {
      currentPage: 1,
      pageSize: 10
    }
  },
  watch: {
    page (oldVal, newVal) {
      if (newVal) {
        this.currentPage = this.page
      }
    }
  },
  created () {
    console.log(this.tableData)
  },
  methods: {
    handleSizeChange (val) {
      this.pageSize = val
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.$emit('changeCurrentPage', this.currentPage)
    },
    check (course) {
      this.$emit('checkCourseDetail', course)
    }
  }
}
</script>
<style scoped>
@import "../../../assets/styles/courses.css"
</style>



